import { create } from 'zustand'
import { persist } from 'zustand/middleware'

export interface MenuItem {
    id: string
    name: string
    price: number
    category: 'COFFEE' | 'LATTE' | 'JUICE' | 'ADE' | 'TEA' | 'SMOOTHIE' | 'FRAPE' | 'DESSERT'
    image: string
    description: string
}

export interface Order {
    id: string
    userId: string
    items: { item: MenuItem; quantity: number }[]
    total: number
    status: 'pending' | 'completed'
    createdAt: Date
}

export interface Coupon {
    id: string
    userId: string
    type: 'free'
    createdAt: Date
}

export interface Stamp {
    id: string
    userId: string
    createdAt: Date
}

export interface User {
    id: string
    username: string
    password: string
    name: string
    createdAt: Date
}

export interface Reservation {
    id: string
    userId: string
    items: { item: MenuItem; quantity: number }[]
    total: number
    status: 'pending' | 'completed'
    createdAt: Date
    pickupTime: Date
}

interface Store {
    menu: MenuItem[]
    cart: { item: MenuItem; quantity: number }[]
    orders: Order[]
    reservations: Reservation[]
    currentUser: User | null
    users: User[]
    stamps: Stamp[]
    coupons: Coupon[]
    userFavorites: Record<string, string[]>
    isAdmin: boolean
    addToCart: (item: MenuItem) => void
    removeFromCart: (itemId: string) => void
    updateQuantity: (itemId: string, quantity: number) => void
    clearCart: () => void
    placeOrder: () => void
    makeReservation: (date: string, time: string, people: number) => void
    cancelReservation: (reservationId: string) => void
    login: (email: string, password: string) => void
    logout: () => void
    register: (email: string, password: string) => void
    addStamp: (userId: string) => void
    useCoupon: (userId: string, couponId: string) => void
    toggleFavorite: (itemId: string) => void
    getUserFavorites: (userId: string) => string[]
    setIsAdmin: (isAdmin: boolean) => void
    setCurrentUser: (user: User | null) => void
    getUserOrders: (userId: string) => Order[]
    getUserCoupons: (userId: string) => Coupon[]
    getUserStamps: (userId: string) => Stamp[]
}

