export const useStore = create<Store>()(
    persist(
        (set, get) => ({
            menu: sampleMenu,
            cart: [],
            orders: [],
            reservations: [],
            currentUser: null,
            users: [],
            stamps: [],
            coupons: [],
            userFavorites: {},
            isAdmin: false,

            addToCart: (item) => {
                if (!item || !item.id) {
                    console.error('Invalid item provided to addToCart:', item);
                    return;
                }
                set((state) => {
                    // Validate existing cart items
                    const validCart = state.cart.filter(cartItem =>
                        cartItem &&
                        cartItem.item &&
                        cartItem.item.id &&
                        typeof cartItem.quantity === 'number'
                    );

                    const existingItem = validCart.find((i) => i.item.id === item.id)
                    if (existingItem) {
                        return {
                            cart: validCart.map((i) =>
                                i.item.id === item.id
                                    ? { ...i, quantity: i.quantity + 1 }
                                    : i
                            ),
                        }
                    }
                    const newItem = {
                        item: {
                            id: item.id,
                            name: item.name,
                            price: item.price,
                            category: item.category,
                            image: item.image,
                            description: item.description
                        },
                        quantity: 1
                    }
                    return { cart: [...validCart, newItem] }
                })
            },

            removeFromCart: (itemId) =>
                set((state) => ({
                    cart: state.cart.filter((i) => i && i.item && i.item.id !== itemId),
                })),

            updateQuantity: (itemId, quantity) =>
                set((state) => ({
                    cart: state.cart.map((i) =>
                        i && i.item && i.item.id === itemId ? { ...i, quantity } : i
                    ).filter(Boolean),
                })),

            clearCart: () => set({ cart: [] }),

            placeOrder: () => {
                const { cart, currentUser } = get()
                if (!currentUser) return

                // Validate cart items before placing order
                const validCart = cart.filter(item =>
                    item &&
                    item.item &&
                    item.item.id &&
                    typeof item.quantity === 'number' &&
                    typeof item.item.price === 'number'
                );

                const order = {
                    id: `order-${Date.now()}`,
                    userId: currentUser.id,
                    items: validCart,
                    total: validCart.reduce((sum, item) => sum + (item.item.price * item.quantity), 0),
                    status: 'pending' as const,
                    createdAt: new Date(),
                }

                // Add stamps for each item in the order
                const newStamps = validCart.flatMap(item =>
                    Array(item.quantity).fill(null).map(() => ({
                        id: `stamp-${Date.now()}-${Math.random()}`,
                        userId: currentUser.id,
                        createdAt: new Date()
                    }))
                );

                set((state) => ({
                    orders: [...state.orders, order],
                    cart: [],
                    stamps: [...state.stamps, ...newStamps]
                }))
            },

            makeReservation: (date, time, people) => {
                const { currentUser } = get()
                if (!currentUser) return

                const reservation = {
                    id: `reservation-${Date.now()}`,
                    userId: currentUser.id,
                    date,
                    time,
                    people,
                    status: 'pending' as const,
                    items: [],
                    total: 0,
                    createdAt: new Date(),
                    pickupTime: new Date(`${date}T${time}`),
                }

                set((state) => ({
                    reservations: [...state.reservations, reservation],
                }))
            },

            cancelReservation: (reservationId) =>
                set((state) => ({
                    reservations: state.reservations.filter(
                        (r) => r.id !== reservationId
                    ),
                })),

            login: (email, password) => {
                const user = get().users.find(
                    (u) => u.username === email && u.password === password
                )
                if (user) {
                    set({ currentUser: user })
                }
            },

            logout: () => {
                set({
                    currentUser: null,
                    isAdmin: false,
                    cart: []
                })
            },

            register: (email, password) => {
                const existingUser = get().users.find((u) => u.username === email)
                if (existingUser) return

                const user = {
                    id: `user-${Date.now()}`,
                    username: email,
                    password,
                    name: email.split('@')[0],
                    createdAt: new Date(),
                }
                set((state) => ({
                    users: [...state.users, user],
                    currentUser: user,
                }))
            },

            addStamp: (userId) => {
                set((state) => ({
                    stamps: [
                        ...state.stamps,
                        {
                            id: `stamp-${Date.now()}`,
                            userId,
                            createdAt: new Date(),
                        },
                    ],
                }))
            },

            useCoupon: (userId, couponId) => {
                set((state) => ({
                    coupons: state.coupons.filter((c) => c.id !== couponId),
                }))
            },

            toggleFavorite: (itemId) =>
                set((state) => {
                    if (!state.currentUser) return state

                    const userId = state.currentUser.id
                    const userFavorites = state.userFavorites[userId] || []

                    return {
                        userFavorites: {
                            ...state.userFavorites,
                            [userId]: userFavorites.includes(itemId)
                                ? userFavorites.filter((id) => id !== itemId)
                                : [...userFavorites, itemId],
                        },
                    }
                }),

            getUserFavorites: (userId) => {
                return get().userFavorites[userId] || []
            },

            setIsAdmin: (isAdmin) => set({ isAdmin }),
            setCurrentUser: (user) => set({ currentUser: user }),

            getUserOrders: (userId) => {
                return get().orders.filter(order => order.userId === userId)
            },

            getUserCoupons: (userId) => {
                return get().coupons.filter(coupon => coupon.userId === userId)
            },

            getUserStamps: (userId) => {
                return get().stamps.filter(stamp => stamp.userId === userId)
            },
        }),
        {
            name: 'campus-cafe-storage',
        }
    )
) 